* {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', Impact, 'Arial Narrow Bold', sans-serif;
}

body {
  background-color: black; /* Set the background color of the body to black */
  color: white; /* Optional: set the txt color to white to contrast with the black background */
}

.main {
  width: 100%;
  height: 100vh;
}

/* Add a specific class for the offers page */
.offers-main {
  width: 100%;
  height: 100vh;
  overflow: hidden;  /* Only prevent scroll on offers page */
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cinestoke {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;

  font-size: 4rem;
  color: white;  /* White text color */
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.2), 0 0 20px rgba(255, 255, 255, 0.2), 0 0 30px rgba(255, 255, 255, 0.2), 0 0 40px rgba(255, 255, 255, 0.2);
}

/* Media query for mobile devices */
@media (max-width: 768px) {
  .cinestoke {
    font-size: 1.8rem;  /* Adjust the font size for smaller screens */
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
}

.background-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

/* B A R S /////////////////////////////////*/

.cinestoke-section {
  width: 80%;
  margin: 1.25rem auto; /* Centers the section */
  display: block; /* Ensures it behaves like a block element */


}

/* First layer  */
.cinestoke-section .mui-box1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
   
}
/* Second layer  */
.cinestoke-section .mui-box2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  border-bottom: 2px solid white;

  border-bottom: 2px solid white;
}

/* Styling for each SVG */
.cinestoke-section .mui-box-item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  height: 80px;
}
/* Styling for TEXT on layer 2 ///////////////////////////////////////////////////////*/
.twotext {
  display: flex;
  justify-content: center;  /* Center horizontally */
  align-items: center;      /* Center vertically */
  height: 100px;
  width: 250px;
}


/* Styling for 'CINESTOKE' text */
.cinestoke-section .cinestoke-text {
  font-size: 60px;
  text-align: center;
  color: white;
  margin: 0;
}
/* Styling for 'Concept to Creation' text */
.cinestoke-section .concept-text {
  font-size: 30px;
  text-align: center;
  color: white;
  margin: 0;
  
  padding-right: 6px; 
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;


}
/* Styling for 'Create Moments, Capture Feelings' text */
.cinestoke-section .moments-text {
  font-size: 25px;
  text-align: center;
  color: white;
  margin: 0;
  
  font-family: Impact, Haettenschweiler, 'Arial Narrow', sans-serif;

 
}
/*///////////////////////////////////////////////////////*/



/* V E R T L I N E S ////////////////////////////////
/* Styling for the vertical lines */
.cinestoke-section .vertical-line {
  width: 2px;
  background-color: white;
  height: 100px; /* Adjust height as needed */
}
.cinestoke-section .vertical-line2 {
  width: 2px;
  background-color: white;
  height: 120px; /* Adjust height as needed */
}
.cinestoke-section .vertical-lineblack {
  width: 2px;
  background-color: black;
  height: 100px; /* Adjust height as needed */
}
.cinestoke-section .vertical-line2black {
  width: 2px;
  background-color: black;
  height: 120px; /* Adjust height as needed */
}







/* C L I E N T S /////////////////////////////////*/
.cinestoke-section svg {
  height: 100px; /* Set the desired height for the images */
  width: auto; /* Maintain the aspect ratio */
  display: block; /* Remove extra spacing caused by inline images */
}
.cinestoke-section .line {
  width: 2px;
  background-color: white;
  height: 100px; /* Adjust height as needed */
}
.cinestoke-section .lineb {
  width: 2px;
  background-color: black;
  height: 100px; /* Adjust height as needed */
}

/* S O C I A L /////////////////////////////////*/
.social-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  transition: all 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
}

.social-button:hover {
  transform: scale(1.1);
}

.social-button svg {
  height: 40px;
  width: auto;
  display: block;
}

.instagram-button svg {
  color: white;
}

.youtube-button svg {
  color: white;
}

.tiktok-button svg {
  color: white;
}

/* Make Instagram icon in Social.jsx much smaller */
.social-button.instagram-button svg {
  width: 40px !important;
  height: 40px !important;
  max-width: 40px !important;
  max-height: 40px !important;
}

/* Make YouTube icon in Social.jsx much smaller */
.social-button.youtube-button svg {
  width: 40px !important;
  height: 40px !important;
  max-width: 40px !important;
  max-height: 40px !important;
}

/* Make TikTok icon in Social.jsx much smaller */
.social-button.tiktok-button svg {
  width: 40px !important;
  height: 40px !important;
  max-width: 40px !important;
  max-height: 40px !important;
}

/* Shorter vertical lines for social section */
.social-section .vertical-line {
  width: 2px;
  background-color: white;
  height: 70px;
}

.social-section .vertical-lineblack {
  width: 2px;
  background-color: black;
  height: 70px;
}

/* Shorter vertical lines for social section */
.social-section .line {
  width: 2px;
  background-color: white;
  height: 60px;
}

.social-section .lineb {
  width: 2px;
  background-color: black;
  height: 60px;
}

/* Make social horizontal bars longer on desktop */
@media (min-width: 769px) {
  .cinestoke-section.social-section .mui-box1 {
    border-top: 2px solid white;
    border-bottom: 2px solid white;
    width: 60vw;
    margin: 0 auto;
    max-width: none;
  }
}

/* Center social section on all devices */
.social-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.social-section .mui-box1 {
  min-width: 300px;
  max-width: 600px;
}

/* Mobile responsive centering */
@media (max-width: 768px) {
  .social-section .mui-box1 {
    width: 80%;
    min-width: 250px;
  }
}

@media (max-width: 480px) {
  .social-section .mui-box1 {
    width: 90%;
    min-width: 200px;
  }
}






/*IMG CAROSEL////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* Style for the carousel container */
.carousel-container {
  position: relative;
  max-width: 100%;
  margin: auto;
  overflow: hidden; /* Hide overflow for a clean layout */
  padding: 10px;
  box-sizing: border-box;
}

/* Style for each SwiperSlide */
.swiper-slide {
  display: flex;
  justify-content: center; /* Center align the items */
  align-items: center;
  padding: 10px;
}

/* Style for the images in the swiper slides */
.swiper-slide img {
  max-width: 100%;
  max-height: 500px; /* Restrict image height to maintain consistency */
  object-fit: contain; /* Ensure image maintains aspect ratio */
  transition: transform 0.3s ease; /* Add a smooth transition effect */
}

/* Add hover effect for images */
.swiper-slide img:hover {
  transform: scale(1.05); /* Slight zoom-in effect */
}

/* Style for navigation arrows */
.swiper-button-next, .swiper-button-prev {
  color: #fff; /* White color for the arrows */
  position: absolute;
  top: 50%;
  z-index: 10;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  border-radius: 50%;
  padding: 10px;
}

.swiper-button-next {
  right: 10px;
  color: #fff !important;
}

.swiper-button-prev {
  left: 10px;
  color: #fff !important;
}



.carousel-container {
  .swiper-slide {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Ensures that everything is centered vertically */
  }

  .swiper-slide img {
    max-width: 100%;  /* Ensure image is responsive */
    height: auto;
  }

  .swiper-slide p {
    margin-bottom: 10px;  /* Space between image and text */
    font-size: 30px;   /* Increase the text size */
    color: white;       /* Text color */
    text-align: center; /* Center the text horizontally */
    font-weight: bold;  /* Optional: Make the text bold */
  }
}







@media (max-width: 1000px) {
  .cinestoke-section {
    width: 90%; /* Shrink the width of the entire section */
  }

  .cinestoke-section .mui-box1,
  .cinestoke-section .mui-box2 {
    height: auto; /* Allow the layers to adjust based on their content */
    margin: 0; /* Remove outer space */
    padding: 0; 
    display: flex;
    justify-content: space-around; 
   
  }

  .cinestoke-section .mui-box-item {
    height: auto; /* Ensure items shrink automatically */
    padding: 2%; /* Adjust padding for better spacing */
  }

  .cinestoke-section .cinestoke-text {
    font-size: 6vw; /* Shrink font size */
    padding-left: 10px;
    padding-right: 10px;
  }

  .cinestoke-section .concept-text {
    font-size: .9rem; /* Adjust text size for smaller screens */
  }
  .cinestoke-section .moments-text {
    font-size: .8rem; /* Adjust text size for smaller screens */
  }

  .cinestoke-section .vertical-line,
  .cinestoke-section .vertical-lineblack{
    height: 75px; /* Shrink vertical line height */
  }
  .cinestoke-section .vertical-line2,
  .cinestoke-section .vertical-line2black {
    height: 75px; /* Shrink vertical line height */
  }

  .cinestoke-section .film-icon,
  .cinestoke-section .drone-icon,
  .cinestoke-section .edit-icon,
  .cinestoke-section .shutter-icon {
    width: 75px; /* Shrink icons proportionally */
    height: 75px;
  }

  .cinestoke-section .mui-box-item2 {
    height: 0px; /* Adjust height automatically */
    width: 70%; /* Adjust width for smaller screens */
  }

  .twotext {
    display: flex;
    justify-content: center;  /* Center horizontally */
    align-items: center;      /* Center vertically */
    height: 30px;
    width: 20vw;
  }
  .cinestoke-section .wings-icon {
    width: 150px;
    height: 75px;
  }


  .cinestoke-section svg {
    height: 60px; /* Set the desired height for the images */
    width: auto; /* Maintain the aspect ratio */
    display: block; /* Remove extra spacing caused by inline images */
  }
  .cinestoke-section .line {
    width: 2px;
    background-color: white;
    height: 60px; /* Adjust height as needed */
  }
  .cinestoke-section .lineb {
    width: 2px;
    background-color: black;
    height: 60px; /* Adjust height as needed */
  }

}


@media (max-width: 768px) {
  /*////////////////Carosel/////////////////////////////////////////*/
  .carousel-container {

    .swiper-slide p {
      margin-bottom: 10px;  /* Space between image and text */
      font-size: 20px;   /* Increase the text size */
      color: white;       /* Text color */
      text-align: center; /* Center the text horizontally */
      font-weight: bold;  /* Optional: Make the text bold */
    }
  }
  .swiper-slide {
    padding: 5px;
  }


  .swiper-button-next {
    right: 10px;
    color: #fff !important;
    opacity: 0;
  }
  
  .swiper-button-prev {
    left: 10px;
    color: #fff !important;
    opacity: 0;
  }
}


@media (max-width: 480px) {
  /*////////////////Carosel/////////////////////////////////////////*/
  .carousel-container {

    .swiper-slide p {
      margin-bottom: 10px;  /* Space between image and text */
      font-size: 14px;   /* Increase the text size */
      color: white;       /* Text color */
      text-align: center; /* Center the text horizontally */
      font-weight: bold;  /* Optional: Make the text bold */
    }
  }
  .swiper-slide {
    padding: 2px;
  }
}

.bottom-logo {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0;
  margin-top: 2rem;  /* Add space above the footer */
}

.bottom-logo img {
  width: 100%;
  height: auto;
  max-width: none;
  display: block;
}

/* Add these styles */
.offers-section {
  padding: 100px 20px;
  max-width: 1400px;
  margin: 0 auto;
}

.offers-section h1 {
  text-align: center;
  font-size: 5.5rem;
  margin-bottom: 0px;
  color: white;
}

.offers-grid {
  display: grid;
  grid-template-columns: repeat(3, 350px);
  justify-content: center;
  gap: 40px;
  padding: 20px;
}

.offer-card {
  background: linear-gradient(145deg, 
    rgba(0, 0, 0, 0.7),
    rgba(0, 0, 0, 0.5)
  );
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 15px;
  padding: 30px;
  text-align: center;
  position: relative;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  cursor: pointer;
  color: white;
}

.offer-card:hover {
  transform: scale(1.02);
}

.offer-card.selected {
  border: 2px solid #FF6B00;
}

.offers-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 50px;
}

.wing {
  width: 160px;
  height: auto;
  flex-shrink: 0;
}

.left-wing {
  transform: translateY(-24px);
}

.right-wing {
  transform: translateY(-24px);
}

@media (max-width: 768px) {
  .wing {
    display: none;
  }
  
  .offers-section h1 {
    font-size: 3.5rem;
  }
}

@media (max-width: 1024px) {
  .offers-grid {
    grid-template-columns: repeat(1, 320px);
  }
}

/* Add back the card styles */
.offer-card h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: white;
}

.offer-card .price {
  font-size: 3rem;
  margin-bottom: 20px;
  color: white;
}

.offer-card ul {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}

.offer-card li {
  margin: 10px 0;
  font-size: 1.2rem;
  color: white;
}

/* Update mobile styles for offers */
@media (max-width: 768px) {
  .wing {
    display: none;
  }
  
  .offers-section h1 {
    font-size: 3.5rem;
  }
  
  .offer-card {
    padding: 20px;
  }
  
  .offer-card h2 {
    font-size: 1.5rem;
  }
  
  .offer-card .price {
    font-size: 2rem;
  }
  
  .offer-card li {
    font-size: 1rem;
  }
}

@media (max-width: 1024px) {
  .offers-grid {
    grid-template-columns: repeat(1, 320px);
  }
  
  .offers-main {
    height: auto;
    overflow: visible;  /* Allow scroll when cards stack */
  }
}